
  // initially any map object has left top corner at lat lng coordinates
  // it's on you to set object origin to 0,0 coordinates

.markerDiv:hover .popupComment p{
    display: block;
    animation: fadeIn .5s ease;
    // visibility: visible;
    // opacity: 1;
}

.markerDiv:hover .markerImage {
    transition: .25s ease;
    width: 65px;
    height: auto;
    left: -32.5px;
    top: -79.3px;
    z-index: 10;
}

.popupComment p{
    background-color: white;
    padding: .5rem;
    border-radius: 5px;
    width: 100px;
    height: auto;
    position: absolute;
    left: -20px;
    top: -130px;
    z-index: 15;
    transition: .25s ease;
    // transition: transform 0.25s cubic-bezier(0.485, 1.650, 0.545, 0.835);
    // -webkit-transition: -webkit-transform 0.25s cubic-bezier(0.485, 1.650, 0.545, 0.835);
    display: none;
    // visibility: hidden;
    // opacity: 0;
}

.popupComment p:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 30%;
  margin-left: -15px;
  border: 10px solid transparent;
  border-top: 10px solid white;
}

.markerImage {
    width: 50px;
    height: auto;
    position: absolute;
    left: -25px;
    top: -61px;
}

// .fade_in {
//     animation: fadeIn 2s ease;
// }

@keyframes fadeIn {
  0% { opacity: 0 }
  100% { opacity: 1 }
}
